<template>
  <div>
    {{test}}
    <!-- {{width}} -->
    <b-row>
      <b-button variant="success" @click="next('#/scanlogin')">ScanLogin</b-button>&nbsp;
      <b-button variant="warning" @click="next('#/scangoods')">ScanGoods</b-button>
    </b-row>
    <b-row>
      <b-button size="sm" @click="nextday(-1)">-</b-button>
      <b-col sm="2">
        <b-form-input size="sm" type="date" v-model="currentDate" class="text-center"></b-form-input>
      </b-col>
      <b-button size="sm" @click="nextday(1)">+</b-button>
    </b-row>
    <b-row>
      <b-table small striped hover :items="items" :fields="fields">
        <template v-slot:cell(qr)="data">
          <div class="text-nowrap">
            <b-button size="sm" @click="deletebyid(data.item.id)">X</b-button>
            <label v-if="data.item.status==0" style="color:green;">{{data.item.qr}}</label>
            <label v-else style="color:red;">{{data.item.qr}}</label>
          </div>
        </template>
        <template v-slot:cell(login)="data">
          <b-form-input size="sm" type="datetime" v-model="data.item.login"></b-form-input>
        </template>
        <template v-slot:cell(logout)="data">
          <b-form-input size="sm" type="datetime" v-model="data.item.logout"></b-form-input>
        </template>
        <template v-slot:cell(minute)="data">
          <b-form-input size="sm" type="number" v-model="data.item.minute"></b-form-input>
        </template>
        <template v-slot:cell(price)="data">
          <b-form-input size="sm" type="number" v-model="data.item.price"></b-form-input>
        </template>
        <template v-slot:cell(goods8)="data">
          <b-form-input size="sm" type="number" v-model="data.item.goods8"></b-form-input>
        </template>
        <template v-slot:cell(goods13)="data">
          <b-form-input size="sm" type="number" v-model="data.item.goods13"></b-form-input>
        </template>
        <template v-slot:cell(goods15)="data">
          <b-form-input size="sm" type="number" v-model="data.item.goods15"></b-form-input>
        </template>
        <template v-slot:cell(goodsother)="data">
          <b-form-input size="sm" type="number" v-model="data.item.goodsother"></b-form-input>
        </template>
        <template v-slot:cell(total)="data">
          <b-form-input
            size="sm"
            type="number"
            v-model="data.item.total"
            style="background-color:white;color:green;font-weight: bold;"
          ></b-form-input>
        </template>
        <template v-slot:cell(update)="data">
          <div class="text-nowrap">
            <b-button size="sm" variant="primary" @click="updatebyid(data.item)">Update</b-button>
          </div>
        </template>
        <!-- <template v-slot:cell(status)="data">
          <b-form-input size="sm" type="number" v-model="data.item.status"></b-form-input>
        </template>-->
      </b-table>
    </b-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      width: 0,
      currentDate: "",
      test: "",
      fields: [
        {
          key: "qr",
          label: "QR",
          sortable: true
        },
        {
          key: "login",
          label: "Login",
          sortable: true
        },
        {
          key: "logout",
          label: "Logout"
        },
        {
          key: "minute",
          label: "นาที",
          sortable: true
        },
        {
          key: "price",
          label: "ค่าบริการ",
          sortable: true
        },
        {
          key: "goods8",
          label: "#8"
        },
        {
          key: "goods13",
          label: "#13"
        },
        {
          key: "goods15",
          label: "#15"
        },
        {
          key: "goodsother",
          label: "#"
        },
        {
          key: "total",
          label: "รวมค่าใช้จ่าย",
          sortable: true
        },
        {
          key: "update",
          label: "update"
        }
      ],
      items: []
    };
  },
  mounted() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
    this.currentDate = new Date().toJSON().slice(0, 10);
    this.getbydate();
  },
  watch: {
    items: {
      handler(val) {
        for (let i = 0; i < this.items.length; i++) {
          this.items[i].total =
            parseFloat(this.items[i].price) +
            parseFloat(this.items[i].goods8 * 8) +
            parseFloat(this.items[i].goods13 * 13) +
            parseFloat(this.items[i].goods15 * 15) +
            parseFloat(this.items[i].goodsother);
        }
      },
      deep: true
    }
  },
  methods: {
    handleResize() {
      this.width = window.innerWidth;
    },
    nextday(day) {
      var d = new Date(this.currentDate);
      var nd = new Date(d);
      nd.setDate(d.getDate() + day);
      this.currentDate = nd.toJSON().slice(0, 10);
      this.getbydate();
    },
    updatebyid(item) {
      const formData = new FormData();
      formData.append("api", "update");
      formData.append("item", JSON.stringify(item));
      axios
        .post(this.$store.state.api, formData)
        .then(response => {
          this.$parent.showAlert(response.data.data, "success");
          this.getbydate();
        })
        .catch(e => {});
    },
    deletebyid(id) {
      const formData = new FormData();
      formData.append("api", "delete");
      formData.append("id", id);
      axios
        .post(this.$store.state.api, formData)
        .then(response => {
          this.$parent.showAlert(response.data.data, "success");
          this.getbydate();
        })
        .catch(e => {});
    },
    getbydate() {
      const formData = new FormData();
      formData.append("api", "list");
      formData.append("date", this.currentDate);
      axios
        .post(this.$store.state.api, formData)
        .then(response => {
          var rows = response.data.rows;
          this.items = rows;
          this.items = [];
          for (let i = 0; i < rows.length; i++) {
            this.items.push({
              id: rows[i].id,
              qr: rows[i].qr,
              login: rows[i].login,
              logout: rows[i].logout,
              minute: rows[i].minute,
              price: rows[i].price,
              goods8: rows[i].goods8,
              goods13: rows[i].goods13,
              goods15: rows[i].goods15,
              goodsother: rows[i].goodsother,
              total: rows[i].total,
              status: rows[i].status,
              update: rows[i].id
            });
          }
        })
        .catch(e => {});
    },
    next(web) {
      window.location.href = web;
    }
  }
};
</script>