import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/test',
      name: 'test',
      component: () => import('./App.vue')
    },
    {
      path: '/scanlogin',
      name: 'scanlogin',
      component: () => import('./views/ScanLogin.vue')
    },
    {
      path: '/scangoods',
      name: 'scangoods',
      component: () => import('./views/ScanGoods.vue')
    },
    {
      path: '/change',
      name: 'change',
      component: () => import('./views/Change.vue')
    }
  ]
})
